/* --------------------------------Cards------------------------------- */

#c-img {
    width: 100%;
    border-radius: 12px;
    height: 800px;
    object-fit: cover;
}


.c-body {
    background-color: #1fa5da;
}

#card-container {
  max-width: 800px;
  margin: 0 auto;
  margin-top: 10vh;
}

.card {
  background-color: white;
  padding: 10px;
  text-align: center;
  border-radius: 12px;
  border: 1px solid #BACDD8;
}

.tag {
  font-family: 'Raleway', sans-serif;
  padding: 4px 8px;
  border: 1px solid #E5EAED;
  margin-top: 3px;
  margin-left: 3px;
  margin-right: 3px;
  border-radius: 50px;
  font-size: 12px;
  font-weight: 600;
  color: #788697;

}

.name {
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
  font-weight: 600;
  margin-top: 16px;
}

p {
  font-family: 'Raleway', sans-serif;
  font-size: 14px;
  color: #7F8C9B;
  line-height: 150%;
}

button {
  border: none;
  padding: none;
}

